import { useState } from 'react';
import axios from 'axios';
import Food from './Food';
import { v4 as uuidv4 } from 'uuid';
import { FaSearch } from 'react-icons/fa';


const Recipe = () => {

    const [query, setQuery] = useState();
    const [result, setResult] = useState([]);

    const APP_ID = '9f261685';
    const APP_KEY = 'f7be9b8e1c0c1a10e0af95e2956eace8';
    const url = `https://api.edamam.com/search?q=${query}&app_id=${APP_ID}&app_key=${APP_KEY}`;

    const getData = async () => {
        const response = await axios.get(url);
        setResult(response.data.hits);
    }
    const onChange = (e) => {
        setQuery(e.target.value);
    }
    const onSubmit = (e) => {
        e.preventDefault();
        getData();
    }
    return (
        <>
                <div className="food-search">
                    <form onSubmit={onSubmit}>
                        <input type="text" placeholder="Food Name" onChange={onChange} />
                        <button type="Submit"><FaSearch /></button>
                    </form>
                </div>
                <div className="foods-here">
                    {result !== [] &&
                        result.map(items => <Food key={uuidv4()} items={items} />)}
                </div>
        </>
    )
}

export default Recipe;