import {FaBars, FaRegHandPointLeft}  from 'react-icons/fa';
import {Link} from 'react-router-dom';


const Menu = () => {
    
    const openNav=(e)=>{
        e.preventDefault();
        document.getElementById("myNav").style.width = "100%";
    }

    const closeNav=()=>{
        document.getElementById("myNav").style.width = "0%";
    }
    
    return (
        <>
            <div className="navBar">
                <Link to="/">APIWEB</Link>
                <ul>
                    <li><Link to="/findrecipe">FIND RECIPE</Link></li>
                    <li id="bar" onClick={openNav}><FaBars/></li>
                </ul>
            </div>
            <div className="sideNav" id="myNav">
                <ul>
                    <li onClick={closeNav}><FaRegHandPointLeft/></li>
                    <li onClick={closeNav}><Link to="/findrecipe" target="_blank">FIND RECIPE</Link></li>
                </ul>
            </div>
            
        </>
    )
}

export default Menu;